.main-container {
  color: #041827;
  flex-grow: 1;
  margin: 0 auto;
  width: 100%;
  min-width: 1024px;
  background-color: #00b3ff;

  @media (max-width: $vp-1023) {
    max-width: 1023px;
    min-width: 768px;
  }

  @media (max-width: $vp-767) {
    max-width: 767px;
    min-width: 320px;
  }
}
